import React, {PureComponent} from 'react'
import {
  Table
} from 'antd'
import moment from 'moment'
export default class TableComponent extends PureComponent {
  constructor() {
    super()

    this.state = {
      dataSources: [], // 原数据
      columns: [], // table 头
      x: 2300
    }
  }
  onChange(pagination, filters, sorter) {
    console.log('params', pagination, filters, sorter)
  }
  componentDidMount() {
    const { superClass } = this.props
    this.getColumnsData(superClass)
    let columnsObj = this.getColumnsData(superClass)
    this.setState({
      ...columnsObj
    })
  }

  getColumnsData(type) {
    // 1 供应商订单
    // 3 配送订单
    //2400
    switch (type) {
      case '1':
        return {columns: [{
          title: '序号',
         
          width: 100,
          key: 'shopId',
          render: (text, record, index) => index + 1
        }, {
          title: '商品编码',
          width: 100,
         
          dataIndex: 'productCode',
          key: 'productCode'
        }, {
          title: '商品名称',
          width: 150,
         
          key: 'productName',
          dataIndex: 'productName'
        }, {
          title: '供应商编码',
          width: 100,
          key: 'supplierCode',
          dataIndex: 'supplierCode'
        }, {
          title: '供应商名称',
          width: 150,
          key: 'supplierName',
          dataIndex: 'supplierName'
        }, {
          title: '件装数',
          width: 100,
          key: 'packQty',
          dataIndex: 'packQty'
        }, {
          title: '单位',
          width: 100,
          key: 'unit',
          dataIndex: 'unit'
        }, {
          title: '数量',
          width: 100,
          key: 'qty',
          dataIndex: 'qty'
        }, {
          title: '单价（含税）',
          width: 100,
          key: 'priceIncludeTax',
          dataIndex: 'priceIncludeTax',
          render: (text, record, index) => text && text.toString().replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
        }, {
          title: '金额（含税）',
          width: 100,
          key: 'amountIncludeTax',
          dataIndex: 'amountIncludeTax',
          render: (text, record, index) => text && text.toString().replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
        }, {
          title: '入库地点',
          width: 100,
          key: 'targetLocationCode',
          dataIndex: 'targetLocationCode'
        }, {
          title: '入库地点名称',
          width: 150,
          key: 'targetLocationName',
          dataIndex: 'targetLocationName'
        },
        // {
        //   title: '入库库存地点',
        //   width: 100,
        //   key: 'receiveStoreDcCode',
        //   dataIndex: 'receiveStoreDcCode'
        // },
        // {
        //   title: '入库库存地点名称',
        //   width: 150,
        //   key: 'receiveStoreDcName',
        //   dataIndex: 'receiveStoreDcName'
        // },
        {
          title: '预计到货日期',
          width: 100,
          key: 'planArrivalDate',
          dataIndex: 'planArrivalDate',
          render: (text) => {
            return(
              <div>{text && moment(text).format('YYYY-MM-DD')}</div>
            )
          }
        }, {
          title: '定价类型',
          width: 100,
          key: 'fixPriceTypeDesc',
          dataIndex: 'fixPriceTypeDesc'
        }, {
          title: '系统进价',
          width: 100,
          key: 'systemPrice',
          dataIndex: 'systemPrice',
          render: (text, record, index) => text && text.toString().replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
        }, {
          title: '手工价',
          width: 100,
          key: 'manualPrice',
          dataIndex: 'manualPrice',
          render: (text, record, index) => text && text.toString().replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
        }, {
          title: '小类编码',
          width: 100,
          key: 'smallClassCode',
          dataIndex: 'smallClassCode'
        }, {
          title: '小类名称',
          width: 100,
          key: 'smallClassName',
          dataIndex: 'smallClassName'
        }, {
          title: '赠品数量',
          width: 100,
          key: 'giftMark',
          dataIndex: 'giftMark'
        }, {
          title: '备注',
          key: 'remark',
          width: 100,
          dataIndex: 'remark'
        }],
          x: 2400
        }
        //2650
      case '3':
        return {columns: [{
          title: '序号',
         
          width: 100,
          key: 'shopId',
          render: (text, record, index) => index + 1
        }, {
          title: '商品编码',
          width: 100,
         
          dataIndex: 'productCode',
          key: 'productCode'
        }, {
          title: '商品名称',
          width: 150,
         
          key: 'productName',
          dataIndex: 'productName'
        }, {
          title: '件装数',
          width: 100,
          key: 'packQty',
          dataIndex: 'packQty'
        }, {
          title: '单位',
          width: 100,
          key: 'unit',
          dataIndex: 'unit'
        }, {
          title: '数量',
          width: 100,
          key: 'qty',
          dataIndex: 'qty'
        }, {
          title: '单价（含税）',
          width: 100,
          key: 'priceIncludeTax',
          dataIndex: 'priceIncludeTax',
          render: (text, record, index) => text && text.toString().replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
        }, {
          title: '金额（含税）',
          width: 100,
          key: 'amountIncludeTax',
          dataIndex: 'amountIncludeTax',
          render: (text, record, index) => text && text.toString().replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
        }, {
          title: '出库地点',
          width: 100,
          key: 'sourceLocationCode',
          dataIndex: 'sourceLocationCode'
        }, {
          title: '出库地点名称',
          width: 150,
          key: 'sourceLocationName',
          dataIndex: 'sourceLocationName'
        },
        {
          title: '入库地点',
          width: 100,
          key: 'targetLocationCode',
          dataIndex: 'targetLocationCode'
        },
        {
          title: '入库地点名称',
          width: 150,
          key: 'targetLocationName',
          dataIndex: 'targetLocationName'
        },
        {
          title: '预计到货日期',
          width: 100,
          key: 'planArrivalDate',
          dataIndex: 'planArrivalDate'
        }, {
          title: '定价类型',
          width: 100,
          key: 'fixPriceTypeDesc',
          dataIndex: 'fixPriceTypeDesc'
        }, {
          title: '批次价',
          width: 100,
          key: 'dynamicPrice',
          dataIndex: 'dynamicPrice',
          render: (text, record, index) => text && text.toString().replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
        }, {
          title: '系统进价',
          width: 100,
          key: 'systemPrice',
          dataIndex: 'systemPrice',
          render: (text, record, index) => text && text.toString().replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
        }, {
          title: '手工价',
          width: 100,
          key: 'manualPrice',
          dataIndex: 'manualPrice',
          render: (text, record, index) => text && text.toString().replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
        }, {
          title: '小类编码',
          width: 100,
          key: 'smallClassCode',
          dataIndex: 'smallClassCode'
        }, {
          title: '小类名称',
          width: 100,
          key: 'smallClassName',
          dataIndex: 'smallClassName'
        }, {
          title: '赠品数量',
          width: 100,
          key: 'giftMark',
          dataIndex: 'giftMark'
        }, {
          title: '备注',
          width: 100,
          key: 'remark',
          dataIndex: 'remark'
        }],
          x: 2650
        }
    }
  }
  componentDidUpdate(prevProps, prevState) {
    const {
      searchResult,
      superClass
    } = this.props

    if (searchResult == prevProps.searchResult && superClass == prevProps.superClass) return false
    let columnsObj = this.getColumnsData(superClass)
    // 在数据中添加字段
    searchResult.map((item, index) => {
      item.key = index
    })
    this.setState({
      dataSources: searchResult,
      ...columnsObj
    })
  }
  render() {
    const {
      pageSizeChange,
      total,
      page,
      size,
      loading,
      superClass
    } = this.props
    const {x} = this.state
    /* 选择功能的配置 */
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      onShowSizeChange: pageSizeChange, // pageSize 变化的回调
      className: 'pagination',
      onChange: pageSizeChange,
      pageSizeOptions: ['50', '100', '200', '500']
    }
    return (
      <div className='table-group'>
        {
          superClass? <Table
            loading={loading}
            columns={this.state.columns}
            dataSource={this.state.dataSources}
            scroll={{x, y: 400}}
            onChange={() => {
              this.onChange()
            }}
            pagination={false}
          /> : null
        }
      </div>
    )
  }
}



