import React, { PureComponent } from 'react'
import {
  Form,
  Row,
  Col,
  Checkbox,
  Select
} from 'antd'
// import moment from 'moment'
import style from '../style/Form.scss'
import { API_SUPERCLASS_KEYVALUES } from 'api'
import { getData } from 'util';
import CsxSelect from '../../../../components/Form/CsxSelect'
import CsxTextArea from '../../../../components/Form/CsxTextArea'

const FormItem = Form.Item

class App extends PureComponent {
  constructor() {
    super()
    this.state = {
      placePurchaseFlag: 0,
      selectData: [],
      defaultValue: '',
    }
  }

  componentDidMount() {
    this.getChangeKeys()
  }

  getChangeKeys = () => {
    getData({
      api: API_SUPERCLASS_KEYVALUES,
      searchParams: { type: 'PI' }
    }).then((res) => {
      const { result } = res
      this.setState({
        selectData: [...result],
        defaultValue: '1'
      })
    })
  }

  checkBoxChange = (e) => {
    this.props.checkBoxChange(e)
  }

  // 可输入的选项值改变的函数
  onInputChange(values) {
    const { onFormChange } = this.props
    onFormChange(values)
  }
  render() {
    const {
      superClass,
      remark,
      isDisplayCheckBox,
      isOnlyDisplayTransfer
    } = this.props
    console.log(this.props)
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 4 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 10 },
      },
    }

    const { selectData, defaultValue } = this.state

    return (
      <Form className={style.form}>
        <Row span={24}>
          <Col span={10}>
            <div style={{ marginBottom: '10px' }}>
              <label style={{ width: '100px', textAlign: 'right', float: 'left' }}>单据类别：</label>
              <div style={{ float: "left", width: "30%", marginRight: '5px' }}>
                {
                  isOnlyDisplayTransfer
                    ? <Select
                      defaultValue={'3'}
                      onChange={(value) => {
                        this.onInputChange({ superClass: value })
                      }}
                    >
                      <Select.Option value='3'>调拨订单</Select.Option>
                    </Select>
                    : <Select
                      defaultValue={'1'}
                      onChange={(value) => {
                        this.onInputChange({ superClass: value })
                      }}
                    >
                      <Select.Option value='1'>供应商订单</Select.Option>
                      <Select.Option value='3'>调拨订单</Select.Option>
                    </Select>
                }
              </div>
              {
                isDisplayCheckBox ?
                  <div>
                    <Checkbox onChange={this.checkBoxChange}>地采标识</Checkbox>
                  </div>
                  : null
              }
            </div>
          </Col>
        </Row>
        <Row span={24}>
          <Col span={10}>
            <CsxTextArea
              {...formItemLayout}
              formItemLayout={formItemLayout}
              label='备注'
              style={{ width: '600px', minHeight: '100px', marginTop: '10px' }}
              remark={remark}
              number={100}
              onChange={(value) => {
                this.onInputChange({ remark: value })
              }}
            />
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



