import {Bread} from 'Bread'
import React, {PureComponent} from 'react'
import {connect} from 'react-redux'
import {poMangeQueryType} from './redux/action'
import {API_SCM_IMPORT_TEMPLATE} from 'api'
import {getData} from 'util'
import {formatDownload} from '../global/components/menuData'
import {Form, Table, Tool} from './components'

export default class App extends PureComponent {
  constructor(props) {
    super(props)
    this.isTransferImport = this.props.match.path.includes('transferWare')
    console.log(this.props.match.path.includes('transferWare'))
    const superClass = this.isTransferImport ? '3' : '1'
    this.state = {
      searchResult: [], // 获取查询结果
      total: 0, // 查询总数
      page: 1, // 当前页码
      size: 50, // 当前分页数
      tableLoading: false, // 列表loading
      superClass, // 单据的类别
      remark: '', //备注
      placePurchaseFlag: 0
    }
  }

  componentDidMount() {
    // execl模板下载数据
    getData({api: API_SCM_IMPORT_TEMPLATE}).then(({result}) => {
      const downloadData = formatDownload(result)
      localStorage.setItem('b2bScmDownload', JSON.stringify(downloadData))
    })
  }
  // 可输入的选项值改变的函数
  onFormChange = (values) => {
    const {superClass} = values
    if (superClass && superClass !== this.state.superClass) {
      values.searchResult = []
    }
    this.setState({
      ...this.state,
      ...values
    })
  }

  checkBoxChange = (e) => {
    this.setState({
      placePurchaseFlag: e.target.checked ? 1 : 0
    })
  }

  /* 页码和每页显示数量改变 */
  pageSizeChange = (current, pageSize) => {
    if (!current || !pageSize) return false
    this.setState({
      page: current,
      size: pageSize
    })
  }
  // 导入数据进表格
  importTableData = (searchResult) => {
    // searchResult table数据
    this.setState({
      searchResult
    })
  }
  // 清空表格数据
  clearTableData = () => {
    this.setState({
      searchResult: []
    })
  }

  render() {
    const {
      searchResult,
      total,
      page,
      size,
      tableLoading,
      remark,
      placePurchaseFlag,
      superClass
    } = this.state

    return (
      <section>
        {this.isTransferImport ? (
          <Bread route={['调拨管理', '仓间调拨']} />
        ) : (
          <Bread route={['仓间调拨', '采购订单导入']} />
        )}
        <Form
          onFormChange={this.onFormChange}
          checkBoxChange={this.checkBoxChange}
          superClass={superClass}
          isDisplayCheckBox={!this.isTransferImport}
          isOnlyDisplayTransfer={this.isTransferImport}
          remark={remark}
        />
        <Tool
          superClass={superClass}
          placePurchaseFlag={placePurchaseFlag}
          remark={remark}
          importTableData={this.importTableData}
          clearTableData={this.clearTableData}
          searchResult={searchResult}
          onFormChange={this.onFormChange}
          isPomangermentImport={this.props.isPomangermentImport}
        />
        <Table
          loading={tableLoading}
          total={total}
          page={page}
          size={size}
          searchResult={searchResult}
          pageSizeChange={this.pageSizeChange}
          superClass={superClass}
        />
      </section>
    )
  }
}



