import React, {PureComponent} from 'react'
import {
  Button,
  message,
  Modal
} from 'antd'
import {
  API_PURCHASE_ORDER_IMPORT,
  API_PURCHASE_ORDER_SUBMIT
} from 'api'
import {dropByCacheKey} from 'react-router-cache-route'
import {getData} from 'util'
import {Import} from 'HOC'
import style from '../style/Tool.scss'
const confirm = Modal.confirm

// window.onbeforeunload = function (e) {

// }
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
      visible: false, // 是否显示弹出框
      submitInit: false, // 是否可以提交订单
      type: '0', // 弹框类型
      importLoading: false,
      submitLoading: false
    }
  }
  /* 下载模版 */
  handleDownLoad = (key) => {
    const downloadData = JSON.parse(localStorage.getItem('b2bScmDownload'))
    window.open(downloadData[key].downloadUrl)
  }

  // onbeforeunload = (e) => {
  //   e = e || window.event;

  //   // 兼容IE8和Firefox 4之前的版本
  //   if (e) {
  //     e.returnValue = '关闭提示';
  //   }
  //   // message.error('....')
  //   // Chrome, Safari, Firefox 4+, Opera 12+ , IE 9+
  //   return '关闭提示';
  // }

  /* 导入成功 */
  handleImportSuccess = res => {
    res && res.message === 'ok' && message.success('导入成功')
    if (res.result && res.result.data && res.result.data.length > 0) {
      this.setState({
        submitInit: true,
        importLoading: false
      })
    }
    this.props.onFormChange({tableLoading: false})
    this.props.importTableData(res.result.data)
    // window.onbeforeunload = this.onbeforeunload
  }

  // 清空数据
  clearTableData = () => {
    const {clearTableData} = this.props
    this.setState({
      submitInit: false
    }, () => {
      clearTableData()
    })
  }
  /* 报错导出 */
  handleExprotError = () => {
  }
  // 提交订单 againTry // 判断是否是继续提交~
  submitOrder = (againTry) => {
    const {remark, superClass, searchResult, placePurchaseFlag} = this.props
    if (searchResult && searchResult.length == 0) {
      message.error('暂无要导入的数据')
      return
    }
    if (!superClass) {
      message.error('请选择单据类别')
      return
    }
    let reqData = {
      header: {
        superClass,
        remark,
        placePurchaseFlag
      },
      items: searchResult
    }
    if (againTry === 'againSubmit') {
      reqData.confirm = true
    }
    this.setState({
      submitLoading: true
    })

    getData({api: API_PURCHASE_ORDER_SUBMIT, data: reqData}).then(data => {
      if (data.code == '200') {
        this.setState({
          submitInit: false,
          importLoading: false,
          submitLoading: false
        }, () => {
          dropByCacheKey('/scm/poManagement/query')
        })
        Modal.success({
          title: '提示',
          content: `${data.result}创建成功`,
          okText: '我知道了'
        })
      } else if (data.code == '20001005') {
        let _this = this
        confirm({
          title: '温馨提示',
          content: <div className="five-content">
            <ul>
              {data.result.map((o, i) => <li key={i}>{o}</li>)}
            </ul>
            <div className="five-tit">继续提交请点击"确认"，返回修改请点击"返回修改"</div>
          </div>,
          okText: '确认',
          width: 520,
          cancelText: '返回修改',
          onOk() {
            _this.submitOrder('againSubmit')
          },
          onCancel() {
            _this.setState({
              submitLoading: false
            })
          }
        })
      } else {
        message.error(data.message)
      }
    }).catch((err) => {
      this.setState({
        submitLoading: false
      })
    })
  }

  componentWillUnmount() {
    // window.onbeforeunload = this.onbeforeunload
  }
  render() {
    const {
      superClass,
      onFormChange
    } = this.props
    const {submitInit} = this.state
    return (
      <div className={style['table-tool']}>
        <Button type="primary" className={style.btn} onClick={this.clearTableData}>清空数据</Button>
        {
          superClass ? <Import
            action={{api: API_PURCHASE_ORDER_IMPORT, data: {superClass}}}
            onOk={this.handleImportSuccess}
            onExprotError={this.handleExprotError}
            beforeImport={() => {
              this.setState({
                importLoading: true
              })
              onFormChange({tableLoading: true, searchResult: []})
            }}
            onResponse={() => {
              this.setState({
                importLoading: false
              })
              onFormChange({tableLoading: false})
            }}
          // onError={() => {
          //   this.setState({
          //     importLoading: false
          //   })
          // }}
          >
            <Button type="primary" className={style.btn} loading={this.state.importLoading}>导入订单</Button>
          </Import> : <Button type="primary" className={style.btn} onClick={() => {
            message.error('请选择单据类别')
          }}>导入订单</Button>
        }
        {
          submitInit ? <Button type="primary" className={style.btn} onClick={this.submitOrder} loading={this.state.submitLoading}>提交订单</Button> : <Button type="primary" disabled className={style.btn}>提交订单</Button>
        }
        <Button type="primary" className={style.btn} onClick={() => {
          this.handleDownLoad('T17')
        }}>导入模板下载</Button>
        <p className={style.tit}>提示：如关闭本页面，页面数据不再保留</p>
      </div>
    )
  }
}



